html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-outfit);
  background-color: var(--lightgreen);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h3 {
  font-size: 1.375rem;
}

p {
  font-size: 0.9375rem;
  color: var(--grayishblue);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

body {
  font-family: var(--font-outfit);
}

:root {
  --white: hsl(0, 0%, 100%);
  --lightgreen: hsl(212, 45%, 89%);
  --grayishblue: hsl(220, 15%, 55%);
  --darkblue: hsl(218, 44%, 22%);
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 1rem;
  background-color: white;
  border-radius: 1rem;
  max-width: 20.625rem;
  filter: drop-shadow(2px 3px 10px rgba(0, 0, 0, 0.2));
}
.card .card__image {
  height: 100%;
  width: 100%;
  border-radius: 1rem;
  margin-bottom: 1.5rem;
}
.card .card__text-header {
  text-align: center;
  font-weight: 700;
}
.card .card__text-paragraph {
  text-align: center;
}
.card .card__text {
  padding: 0 1rem;
}